import { FaImage, FaTags, FaRegFileAlt } from "react-icons/fa";

const AddProduct = () => {
  return (
    <div className="bg-gray-50 min-h-screen flex flex-col justify-start py-10 px-5">
      <h1 className="text-5xl font-semibold text-center text-white mb-10">Add New Product</h1>

      <form className="w-full flex flex-col gap-8 px-4 sm:px-10 py-5 bg-white shadow-lg">
        {/* Product Details */}
        <div className="flex flex-col gap-4">
          <label className="text-lg font-medium text-gray-700">Product Name</label>
          <input
            type="text"
            placeholder="Enter product name"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />

          <label className="text-lg font-medium text-gray-700">Slug</label>
          <input
            type="text"
            placeholder="Enter product slug (e.g. my-product)"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />

          <label className="text-lg font-medium text-gray-700">Description</label>
          <textarea
            placeholder="Enter product description"
            rows="4"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />

          <label className="text-lg font-medium text-gray-700">Category</label>
          <select className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500">
            <option>Select Category</option>
            <option>Electronics</option>
            <option>Clothing</option>
            <option>Home Appliances</option>
          </select>

          <label className="text-lg font-medium text-gray-700">Tags</label>
          <input
            type="text"
            placeholder="Enter tags (comma separated)"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        {/* Pricing and Inventory */}
        <div className="flex flex-col gap-4 sm:flex-row sm:gap-8">
          <div className="flex-1">
            <label className="text-lg font-medium text-gray-700">Price</label>
            <input
              type="number"
              placeholder="Enter price"
              className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="flex-1">
            <label className="text-lg font-medium text-gray-700">Discount Price</label>
            <input
              type="number"
              placeholder="Enter discount price"
              className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="flex-1">
            <label className="text-lg font-medium text-gray-700">Stock Quantity</label>
            <input
              type="number"
              placeholder="Enter stock quantity"
              className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>
        </div>

        {/* Media Upload */}
        <div className="flex flex-col gap-4">
          <label className="text-lg font-medium text-gray-700">Product Images</label>
          <input
            type="file"
            multiple
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        {/* SEO */}
        <div className="flex flex-col gap-4">
          <label className="text-lg font-medium text-gray-700">Meta Title</label>
          <input
            type="text"
            placeholder="Enter SEO title"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />

          <label className="text-lg font-medium text-gray-700">Meta Description</label>
          <textarea
            placeholder="Enter SEO description"
            rows="3"
            className="w-full p-4 border-2 border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        {/* Submit Button */}
        <div className="flex justify-center mt-8">
          <button
            type="submit"
            className="bg-blue-500 text-white py-4 px-8 rounded-lg shadow-md hover:bg-blue-600 transition duration-200 w-full sm:w-auto flex items-center justify-center"
          >
            <FaRegFileAlt className="mr-3" />
            Submit Product
          </button>
        </div>
      </form>
    </div>
  );
};

export default AddProduct;
